import { formErrors } from "src/config/form";
import * as yup from "yup";

export const validation = yup.object({
  name: yup
    .string()
    .required(formErrors.REQUIRED)
    .max(70, formErrors.NAME_MAX_LENGTH),
  author: yup
    .string()
    .required(formErrors.REQUIRED)
    .max(120, formErrors.AUTHOR_MAX_LENGTH),
  year: yup
    .number()
    .typeError(formErrors.REQUIRED)
    .required(formErrors.REQUIRED)
    .max(2023, formErrors.DATE_DOESNT_EXIST)
    .min(1900, formErrors.DATE_TO_OLD),
});
