import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { deleteBook, getAllBooks } from "src/api";
import { BookInteface } from "src/models/books";
import { setBooks } from "src/modules/book/actions";
import ConfirmDeleteModal from "../modals/ConfirmDelete";
import "./index.scss";

interface Props {
  book: BookInteface;
}

const BooksListItem: React.FC<Props> = ({ book }) => {
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const navigate = useNavigate();
  const dispath = useDispatch();

  const onClose = () => {
    setIsModalOpen(false);
  };

  const onSuccess = async (book: BookInteface) => {
    await deleteBook(book);
    const response = await getAllBooks();
    dispath(setBooks(response));
    setIsModalOpen(false);
  };

  const onEdit = (id: string) => {
    navigate(`/books/${id}/edit`);
  };

  const openBookView = (id: string) => {
    navigate(`/books/${book.id}`);
  };

  return (
    <div
      className="book-item__wrapper"
      onClick={() => {
        openBookView(book.id);
      }}
    >
      <div className="book-item">{book.id}</div>
      <div className="book-item">{book.name}</div>
      <div className="book-item">{book.author}</div>
      <div className="book-item">{book.year}</div>
      <div className="book-item">
        <button
          type="button"
          className="book-item__button"
          onClick={(e) => {
            e.stopPropagation();
            onEdit(book.id);
          }}
        >
          Edit
        </button>
        <button
          type="button"
          className="book-item__button delete"
          onClick={(e) => {
            e.stopPropagation();
            setIsModalOpen(true);
          }}
        >
          Delete
        </button>
      </div>

      {isModalOpen && (
        <ConfirmDeleteModal
          onClose={onClose}
          onSuccess={() => onSuccess(book)}
          name={book.name}
        />
      )}
    </div>
  );
};

export default BooksListItem;
