import "./index.scss";
import React from "react";
import ReactDOM from "react-dom";
import { BookInteface } from "src/models/books";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { validation } from "src/validation/book";
import InputField from "src/components/main/Input";
import { addNewBook, getAllBooks } from "src/api";
import { useDispatch } from "react-redux";
import { setBooks } from "src/modules/book/actions";

interface Props {
  onClose: () => void;
}

const AddNewBookModal: React.FC<Props> = ({ onClose }) => {
  const dispath = useDispatch();
  const form = useForm<BookInteface>({
    defaultValues: {
      id: "",
      name: "",
      author: "",
      year: null,
    },
    resolver: yupResolver(validation),
  });
  form.watch();

  const submit = async () => {
    const formValues = form.getValues();
    await addNewBook(formValues);
    const response = await getAllBooks();
    dispath(setBooks(response));
    onClose();
  };

  return ReactDOM.createPortal(
    <div className="add-new-book-modal" onClick={() => onClose()}>
      <div
        className="add-new-book-modal__content"
        onClick={(e) => e.stopPropagation()}
      >
        <h3 className="add-new-book-modal__title">Add new book</h3>
        <form
          className="add-new-book-modal-form"
          method="post"
          onSubmit={form.handleSubmit(submit)}
        >
          <div className="add-new-book-modal-form__row">
            <InputField
              label="Name"
              type="text"
              name="name"
              defaultValue={form.formState.defaultValues?.name}
              register={form.register}
              error={form.formState.errors.name}
            />
          </div>
          <div className="book-form__row">
            <InputField
              label="Author"
              type="text"
              name="author"
              register={form.register}
              defaultValue={form.formState.defaultValues?.author}
              error={form.formState.errors.author}
            />
          </div>
          <div className="book-form__row">
            <InputField
              label="Publised year"
              type="text"
              name="year"
              register={form.register}
              defaultValue={form.formState.defaultValues?.year}
              error={form.formState.errors.year}
              isNumber
            />
          </div>

          <button type="submit" className="book-form__submit">
            Save
          </button>
        </form>
      </div>
    </div>,
    document.body
  );
};

export default AddNewBookModal;
