import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { BookInteface } from "src/models/books";
import "./index.scss";
import InputField from "../main/Input";
import { editBook } from "src/api";
import Switch from "react-switch";
import { useNavigate } from "react-router-dom";
import { validation } from "src/validation/book";

interface Props {
  book: BookInteface;
}

const BookEditForm: React.FC<Props> = ({ book }) => {
  const [isDisabled, setIsDisabled] = useState<boolean>(true);
  const [baseValues, setBaseValues] = useState<BookInteface>(book);
  const [isToggleActive, setIsToggleActive] = useState<boolean>(false);

  const navigate = useNavigate();

  const form = useForm<BookInteface>({
    defaultValues: {
      ...book,
    },
    resolver: yupResolver(validation),
  });
  form.watch();

  const submit = async () => {
    const formValues = form.getValues();
    await editBook(formValues);
    navigate("/books");
  };

  useEffect(() => {
    if (
      baseValues &&
      JSON.stringify(baseValues) !== JSON.stringify(form.getValues())
    ) {
      setIsDisabled(false);
    } else {
      setIsDisabled(true);
    }
  }, [form.getValues()]);

  return (
    <form
      className="book-form"
      method="post"
      onSubmit={form.handleSubmit(submit)}
    >
      <div className="book-form__row">
        <InputField
          label="Name"
          type="text"
          name="name"
          defaultValue={form.formState.defaultValues?.name}
          register={form.register}
          error={form.formState.errors.name}
        />
      </div>
      <div className="book-form__row">
        <InputField
          label="Author"
          type="text"
          name="author"
          register={form.register}
          defaultValue={form.formState.defaultValues?.author}
          error={form.formState.errors.author}
        />
      </div>
      <div className="book-form__row">
        <InputField
          label="Publised year"
          type="text"
          name="year"
          register={form.register}
          defaultValue={form.formState.defaultValues?.year}
          error={form.formState.errors.year}
          isNumber
        />
      </div>
      <div className="book-form__row">
        <Switch
          checked={isToggleActive}
          onChange={setIsToggleActive}
          checkedIcon={false}
          uncheckedIcon={false}
          onColor="#4e5af2"
          offColor="#6d7895"
        />
      </div>

      <button type="submit" className="book-form__submit" disabled={isDisabled}>
        Save
      </button>
    </form>
  );
};

export default BookEditForm;
