import { useEffect, useState } from "react";
import { Grid } from "react-loader-spinner";
import { useDispatch } from "react-redux";
import { getAllBooks } from "src/api";
import BooksListItem from "src/components/BooksListItem";
import AddNewBookModal from "src/components/modals/AddNewBook";
import { bookTableItem } from "src/constants/books";
import { setBooks } from "src/modules/book/actions";
import { useAppSelector } from "src/store/hooks";
import "./index.scss";

const BookViewPage = () => {
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const books = useAppSelector((state) => state.books.books);

  const dispath = useDispatch();

  useEffect(() => {
    (async () => {
      const response = await getAllBooks();
      dispath(setBooks(response));
    })();
  }, []);

  const onClose = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="books">
      <h1 className="books__title">Your books</h1>

      <div className="books-table">
        <ul className="books-table__top">
          {bookTableItem.map((item, index) => (
            <li className="books-table__top-item" key={index}>
              {item}
            </li>
          ))}
        </ul>
        {books.length ? (
          books.map((item, index) => (
            <BooksListItem book={item} key={item.id} />
          ))
        ) : (
          <div className="loader">
            <Grid
              height="80"
              width="80"
              color="#4e5af2"
              ariaLabel="grid-loading"
              radius="12.5"
              wrapperStyle={{}}
              wrapperClass=""
              visible={true}
            />
          </div>
        )}
      </div>

      <button
        type="button"
        className="books-table__button"
        onClick={() => setIsModalOpen(true)}
      >
        Add new book
      </button>

      {isModalOpen && <AddNewBookModal onClose={onClose} />}
    </div>
  );
};

export default BookViewPage;
