import { BookInteface } from "./models/books";
import { BASE_URL } from "./config";

export const getAllBooks = async (): Promise<BookInteface[]> => {
  const response = await fetch(`${BASE_URL}/books`);

  return response.json();
};

export const getBookById = async (id: string): Promise<BookInteface> => {
  const response = await fetch(`${BASE_URL}/books/${id}`);

  return response.json();
};

export const editBook = async (book: BookInteface): Promise<BookInteface> => {
  const response = await fetch(`${BASE_URL}/books/${book.id}`, {
    method: "PUT",
    headers: { "content-type": "application/json" },
    body: JSON.stringify(book),
  });

  return response.json();
};

export const addNewBook = async (book: BookInteface): Promise<BookInteface[]> => {
  const response = await fetch(`${BASE_URL}/books`, {
    method: "POST",
    headers: { "content-type": "application/json" },
    body: JSON.stringify(book),
  });

  return response.json();
};

export const deleteBook = async (book: BookInteface) => {
  const response = await fetch(`${BASE_URL}/books/${book.id}`, {
    method: "DELETE",
    headers: { "content-type": "application/json" },
  });

  return response.json();
};
