import "./index.scss";
import React from "react";
import ReactDOM from "react-dom";

interface Props {
  onClose: () => void;
  onSuccess: () => void;
  name: string;
}

const ConfirmDeleteModal: React.FC<Props> = ({ onClose, onSuccess, name }) => {
  return ReactDOM.createPortal(
    <div className="confirm-delete-modal" onClick={() => onClose()}>
      <div
        className="confirm-delete-modal__content"
        onClick={(e) => e.stopPropagation()}
      >
        <h2 className="confirm-delete-modal__title">
          Are you sure you want to delete the "
          <span className="confirm-delete-modal__book-name">{name}</span>" book
          ?
        </h2>
        <div className="confirm-delete-modal__buttons">
          <button
            type="button"
            className="confirm-delete-modal__button confirm"
            onClick={() => onSuccess()}
          >
            Yes
          </button>
          <button
            type="button"
            className="confirm-delete-modal__button"
            onClick={() => onClose()}
          >
            No
          </button>
        </div>
      </div>
    </div>,
    document.body
  );
};

export default ConfirmDeleteModal;
