import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { getBookById } from "src/api";
import { BookInteface } from "src/models/books";
import "./index.scss";
import BookEditForm from "src/components/BookEditForm";
import { Grid } from "react-loader-spinner";

const BookEditPage = () => {
  const [book, setBook] = useState<BookInteface | null>(null);
  const { id } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    if (id) {
      (async () => {
        const book = await getBookById(id);
        setBook(book);
      })();
    }
  }, [id]);

  return (
    <div className="book-edit">
      {book ? (
        <>
          <h1 className="book-edit__title">{book.name} (edit)</h1>
          <BookEditForm book={book} />
          <button onClick={() => navigate(-1)} className="book-edit__button">
            Go back
          </button>
        </>
      ) : (
        <div className="loader">
          <Grid
            height="80"
            width="80"
            color="#4e5af2"
            ariaLabel="grid-loading"
            radius="12.5"
            wrapperClass=""
            visible={true}
          />
        </div>
      )}
    </div>
  );
};

export default BookEditPage;
