import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import { getBookById } from "src/api";
import { BookInteface } from "src/models/books";
import "./index.scss";

const BookEditPage = () => {
  const [book, setBook] = useState<BookInteface | null>(null);
  const { id } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    if (id) {
      (async () => {
        const book = await getBookById(id);
        setBook(book);
      })();
    }
  }, [id]);

  return (
    <div className="book-view">
      {book && (
        <>
          <h1 className="book-view__title">{book.name}</h1>
          <div className="book-view__content">
            <div className="book-view__row">Name: {book.name}</div>
            <div className="book-view__row">Author: {book.author}</div>
            <div className="book-view__row">Publised year: {book.year}</div>
            <Link
              to={`/books/${id}/edit`}
              type="button"
              className="book-view__edit"
            >
              Edit
            </Link>
          </div>
          <button onClick={() => navigate(-1)} className="book-view__button">
            Go back
          </button>
        </>
      )}
    </div>
  );
};

export default BookEditPage;
